--------------------------------------------------------------*/

@font-face {
    font-family: 'open_sans_condensed';
    src: url('../../resources/fonts/opensanscondensed-bold-webfont.eot');
    src: url('../../resources/fonts/opensanscondensed-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../../resources/fonts/opensanscondensed-bold-webfont.woff') format('woff'),
         url('../../resources/fonts/opensanscondensed-bold-webfont.ttf') format('truetype'),
         url('../../resources/fonts/opensanscondensed-bold-webfont.svg#open_sans_condensedbold') format('svg');
    font-weight: 600;
    font-style: normal;
}

